.pageable-blocker .pageable-spinner {
  /* Size and position */
  font-size: 70px;
  /* 1em */
  width: 1em;
  height: 1em;
  position: absolute;
  top: 50%;
  left: 50%;
  margin-left: -0.5em;
  margin-top: -1em;
  /* Styles */
  border-radius: 50%;
  background: #85d0e7;
  /* Fallback */
  background: linear-gradient(#85d0e7 50%, #777777 50%), /* First column */ linear-gradient(#777777 50%, #85d0e7 50%);
  /* Second column */
  background-position: 0 0, /* Position of 1st column */ 100% 0;
  /* Position of 2nd column */
  background-size: 50% 100%;
  /* Contraction of "50% 100%, 50% 100%" */
  background-repeat: no-repeat;
  box-shadow: inset 0 0 0 0.12em rgba(0, 0, 0, 0.2), /* Inner border */ 0 0 0 0.12em rgba(255, 255, 255, 0.1);
  /* Outter border */
  opacity: 0.7;
  animation: spinnerRotate 3s infinite alternate;
}
.pageable-blocker .pageable-spinner:after {
  /* Size */
  content: "";
  width: 50%;
  height: 50%;
  /* Perfect centering */
  position: absolute;
  top: 25%;
  left: 25%;
  /* Styles */
  border: 0.12em solid rgba(255, 255, 255, 0.3);
  border-radius: inherit;
}
@keyframes spinnerRotate {
  25% {
    transform: rotate(270deg);
  }
  50% {
    transform: rotate(90deg);
  }
  75% {
    transform: rotate(360deg);
  }
  100% {
    transform: rotate(180deg);
  }
}
